      'declaration': ''
    }
  ]
  declareShowItem:any = {} // 
  activeDeclareIndex :number = 0 // 
  checkAgreeStatus:boolean = false
  iscitydialog:boolean = false
  activeCityName:string = ''
  proCode:string = '' // code
  cityCode:string=''
  activeSchool:string = ''
  proName:string = ''
