            (_that as any).$post('/wx/user/getMediaImage', { media_id: serverId }).then((res:any) => {
              if (res.status === 1) {
                let img:any = res.data
                _that.cropper.replace(img)
                _that.cropper.replace(img)
                _that.cropImageShow = true
              }
            })
          }
        })
      }
    })
    /* var _that:any = this
